<template>
  <v-data-table
    :headers="headers"
    :items="caisses"
    sort-by="calories"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Suivi des caisses pour validation</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-switch
          @change="initialize()"
          v-model="archive"
          color="primary"
        ></v-switch>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon color="primary" class="mr-2" @click="printCaisse(item.id)">
        mdi-eye
      </v-icon>
      <v-icon color="primary" @click="validCaisse(item)"> mdi-check </v-icon>
    </template>
    <template v-slot:item.surPlusEspece="{ item }">
      <v-chip color="primary" v-if="!item.surPlusEspece">Non</v-chip>
      <v-chip color="green" v-if="item.surPlusEspece">oui</v-chip>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize"> Reset </v-btn>
    </template>
  </v-data-table>
</template>

<script>
import apiCaisse from "../services/caisse";
export default {
  data: () => ({
    archive: false,
    headers: [
      {
        text: "NÂ° de caisse",
        align: "start",
        sortable: false,
        value: "id",
      },
      { text: "Date", value: "date" },
      { text: "Utilisateur", value: "user" },
      { text: "Recette espece", value: "caisseEspece.recette" },
      { text: "Cb", value: "cb.total" },
      { text: "Cb s.c", value: "cbsc.total" },
      { text: "Cheque", value: "cheque.total" },
      { text: "Ticket", value: "ticket.total" },
      { text: "Sortie", value: "sortie.total" },
      { text: "Surplus Espece", value: "surPlusEspece" },
      { text: "Actions", value: "actions" },
    ],
    caisses: [],
  }),
  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      var requestOptions = {
        method: "GET",
        redirect: "follow",
      };

      if (!this.archive) {
        fetch("https://mytaskcfg.herokuapp.com/api/caisse", requestOptions)
          .then((response) => response.json())
          .then((result) => (this.caisses = result))
          .catch((error) => console.log("error", error));
      } else {
        fetch(
          "https://mytaskcfg.herokuapp.com/api/caisse/archive/data",
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => (this.caisses = result))
          .catch((error) => console.log("error", error));
      }
    },
    printCaisse(idCaisse) {
      this.$router.push({ name: "Pdf", params: { id: idCaisse } });
    },
    validCaisse(item) {
      var index = this.caisses.indexOf(item);
      if (confirm("Etes-vous de vouloir confirmer cette caisse ?")) {
        apiCaisse
          .validCaisse(item.id)
          .then((result) => {
            if (result) {
              console.log(result);
              this.caisses.splice(index, 1);
            }
          })
          .catch((err) => console.log(err));
      }
    },
  },
};
</script>